.Icon-list a svg {
  fill: var(--icons-color);
  margin: 10px;
  height: 56px;
  width: 56px;
}

.Icon-list a svg:hover {
  -webkit-transform: scale(1.3);
      -ms-transform: scale(1.3);
          transform: scale(1.3);
  -webkit-transition: .2s;
  -o-transition: .2s;
  transition: .2s;
}


